.card-container {
  width: 250px;
  height: 250px;
  margin: 10px;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);
  display: flex;
  border-radius: 5px;
  
  flex-direction:row;
  
  }
.img-style {
  width: 100%;
  height: 100%;
  

  border-radius: 5px;
  }

  .tit-card{
    text-align: center;
    background-color: rgba(0, 0, 0, 0.322);
    color: #fff;
    
    display: flex;
   
  }

  .button-container {
    opacity: 0;
    background: none;
    border: none;
    font-size: 40px;
    color: #000;
    display: flex;
    
    top: 50%;
    left: 50%;
    margin: -25px 0 0 -25px;
    z-index: 100;
  }
  .card-container__info {
    width: 100%;
    height: 100%;
   
    z-index: 10;
    box-sizing: border-box;
    
  }

  .card-rating{
    position: relative;
    display: flex;
    margin-top: 0px;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.322);
    color: #fff;
    padding: 5px;
  }

  .card-genres__style{
    
    display: flex;
    position: relative;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.322);
    color: #fff;
    padding: 5px;
  }